.container {
  width: 100%;
  display: grid;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  gap: 20px;
}

.Cart_box {
  max-width: 1100px;
  padding: 20px;
  width: 95vw;
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-left: auto;
  margin-right: auto;
  background-color: white;
}

.product {
  display: flex;
  justify-content: flex-start;
  flex-wrap: nowrap;
  align-items: center;
  margin: 1%;
  padding: 10px;
}

.p_img {
  width: 130px;
  height: 100%;
}

.p_details {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 31px;
  flex-wrap: wrap;
  column-gap: 8px;
  color: black;
}

.p_details h2 {
  font-size: 1rem;
  color: black;
  text-decoration: none;
}

.remove_button {
  font-size: 1rem;
  font-weight: 500;
  padding: 10px 15px;
  color: rgb(251, 247, 247);
  background-color: rgb(241, 43, 43);
  border-radius: 4px;
  cursor: pointer;
  border: none;
  outline: none;
  transition: 0.3s;
}

.remove_button:hover {
  background: #faf7f7;
  color: rgb(0, 0, 0);
  font-weight: 700;
  border: 1px solid black;
  transform: scale(0.9);
}

.cartsummary {
  display: grid;
  justify-self: end;
  width: 270px;
}

.totals {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.totals span {
  color: red;
  font-size: 1rem;
  font-weight: 600;
}

.totals h2 {
  font-size: 1rem;
  font-weight: 600;
}

.totals p {
  font-size: 1rem;
  font-weight: 600;
}

.cartsummary h2 {
  font-weight: 550;
  padding: 5px;
  flex-wrap: nowrap;
}

.paybutt {
  width: 100%;
  background-color: #3c69a4;
  color: white;
  padding: 10px 0;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s;
}

.paybutt:hover {
  background-color: #8394a9;
  transform: scale(0.9);
}

@media screen and (min-width: 250px) and (max-width: 630px) {
  .remove_button {
    padding: 8px 12px;
  }

  .p_details {
    display: grid;
    gap: 5px;
    padding-right: 0;
  }

  .p_details h2 {
    font-size: 1rem;
  }

  .Cart_box {
    padding: 10px;
  }

  .cartsummary {
    width: 95vw;
  }

  .p_img {
    width: 100px;
  }
}

@media screen and (min-width: 631px) and (max-width: 900px) {
  .p_details {
    column-gap: 30px;
  }
}

@media screen and (min-width: 900px) and (max-width: 950px) {
  .p_details {
    columns: 3;
    column-gap: 10px;
    row-gap: 10px
  }
}

@media screen and (min-width: 950px) and (max-width: 1300px) {
  .p_details {
    column-gap: 30px;
    row-gap: 10px
  }
}